body {
  background: #e6e7e8;
  font-family: 'PT Sans', sans-serif;
  font-size: 13px;
  line-height: normal; }

a {
  color: #797979; }

.hidden-mob {
  @media (max-width: 650px) {
    display: none; } }

.btn, a.btn {
  width: 205px;
  height: 47px;
  background: #ffffff;
  font-size: 18px;
  font-family: Tahoma;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  color: #ff3f4a;
  line-height: 47px;
  border-radius: 22px;
  font-weight: 600;
  text-align: center;
  letter-spacing: 0;
  @media (max-width: 800px) {
    height: 30px;
    line-height: 30px;
    font-size: 16px; }
  &-red {
    width: auto;
    background: #ef3f4a;
    color: #680007;
    padding-left: 28px;
    padding-right: 28px;
    letter-spacing: -0.9px; } }

.mainslider {
  position: relative;
  background: #ff3d4b;
  @media (max-width: 480px) {
    display: none; }
  &__item {
    position: relative;
    img {
      margin-left: auto;
      margin-right: auto;
      display: block; }
    .container {
      position: absolute;
      left: 50%;
      top: 201px;
      transform: translate(-50%, 0);
      color: #fff;
      font-size: 21.83px;
      @media (max-width: 800px) {
        top: 35%; } } }
  &__title {
    font-size: 45.82px;
    line-height: 46px;
    font-weight: 600;
    padding-bottom: 15px;
    @media (max-width: 800px) {
      font-size: 22px;
      line-height: normal; } }
  &__desc {
    font-size: 21.83px;
    line-height: 29px;
    padding-bottom: 35px;
    @media (max-width: 800px) {
      font-size: 14px;
      line-height: normal;
      padding-bottom: 15px; } }
  .slick-arrow {
    display: block;
    z-index: 101;
    margin-top: -73px;
    position: absolute;
    top: 50%;
    background: none;
    text-indent: -9999px;
    border: none;
    &.slick-prev {
      left: 50px;
      @include sprite($slide-prev); }
    &.slick-next {
      right: 56px;
      @include sprite($slide-next); } }
  .slick-dots {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 73px;
    display: block;
    margin: 0;
    text-align: center;
    @media (max-width: 800px) {
      bottom: 10px; }
    li {
      list-style: none;
      margin: 0;
      display: inline-block;
      padding: 0 10.5px;
      button {
        display: inline-block;
        height: 16px;
        width: 16px;
        text-indent: -99999px;
        border: none;
        background: #fff;
        border-radius: 50%;
        outline: none; }
      &.slick-active {
        button {
          background: #2e2e2e; } } } } }


.main-text {
  padding: 37px 0 40px;
  background: #a73138;
  text-align: center;
  font-size: 15px;
  color: #ffffff;
  @media (max-width: 800px) {
    font-size: 14px;
    line-height: normal;
    padding:  15px 0;
    br {
      display: none; } }
  @media (max-width: 480px) {
    font-size: 12px; }
  &__title {
    font-size: 36px;
    color: #ffffff;
    font-weight: 600;
    margin-bottom: 17px;
    line-height: 45px;
    letter-spacing: 2px;
    @media (max-width: 800px) {
      font-size: 35px;
      line-height: 35px; }
    @media (max-width: 480px) {
      font-size: 20px;
      line-height: 20px; } }
  p {
    line-height: 19px;
    margin-bottom: 17px;
    @media (max-width: 480px) {
      margin-bottom: 5px; } } }

.main-info {
  background: #64bc46;
  padding: 14px 0 18px;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  &__pic {
    float: left;
    @media (max-width: 640px) {
      margin-bottom: 10px;
      clear: both; } }
  &__text {
    margin-left: 115px;
    font-size: 16px;
    font-weight: 600;
    padding-top: 15px;
    font-family: Tahoma;
    @media (max-width: 800px) {
      font-size: 14px; } } }

h1 {
  font-size: 46px;
  font-weight: 600;
  color: #ef3f4a;
  margin-bottom: 13px; }

main {
  padding-top: 50px;
  @media (max-width: 800px) {
    padding-top: 10px; } }

.catalog-menu {
  display: block;
  margin-bottom: 12px;
  ul {
    display: block;
    margin: 0;
    padding: 0;
    &:after {
      content: '';
      display: table;
      clear: both; }
    li {
      display: block;
      list-style: none;
      float: left;
      margin: 0 7px 10px 0;
      line-height: 29px;
      padding: 0 14px;
      background: #ffffff;
      border-radius: 12px;
      font-family: Tahoma;
      font-size: 14.26px;
      color: #797979;
      font-weight: 600;
      a {
        font-family: Tahoma;
        font-size: 14.26px;
        color: #797979;
        display: inline-block;
        font-weight: 600; } } } }


.catalog-list {
  width: 100%;
  h3 {
    font-size: 22.5px;
    color: #797979;
    font-weight: 600; }
  &__title {
    font-size: 36px;
    color: #303030;
    font-weight: 600;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 28px; }
  .slick-list {
    max-width: 940px;
    margin-left: auto;
    margin-right: auto;
    @media (max-width: 800px) {
      max-width: 720px;
      margin-left: auto;
      margin-right: auto; }
    @media (max-width: 640px) {
      max-width: 470px;
      margin-left: auto;
      margin-right: auto; }
    @media (max-width: 480px) {
      max-width: 230px;
      margin-left: auto;
      margin-right: auto; } }
  &__slider {
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 28px;
    position: relative;
    &:after {
      content: '';
      display: block;
      clear: both; }
    .slick-arrow {
      position: absolute;
      top: 50%;
      border: none;
      background: none;
      text-indent: -9999px;
      cursor: pointer;
      outline: none;
      display: inline-block;
      &.slick-prev {
        left: -50px;
        @include sprite($st-perv);
        @media (max-width: 480px) {
          left: -10px; } }
      &.slick-next {
        right: -50px;
        @include sprite($st-next);
        @media (max-width: 480px) {
          right: -10px; } } } }
  &__item {
    max-width: 212px;
    background: #fff;
    padding: 22px;
    margin-left: 10px;
    margin-right: 10px;
    min-height: 364px;
    margin-bottom: 20px;
    box-shadow: 0px 5px 7px 0px rgba(22, 22, 22, 0.05);
    &-pic {
      margin-bottom: 11px;
      position: relative;
      img {
        display: block;
        margin-left: auto;
        margin-right: auto; } }
    &-title {
      font-size: 18px;
      color: #000000;
      font-weight: bold;
      text-decoration: none;
      margin-bottom: 8px;
      a {
        font-size: 18px;
        color: #000000;
        font-weight: bold;
        text-decoration: none; } }
    &-desc {
      max-width: 185px;
      font-size: 12px;
      color: #000000;
      table {
        td {
          width: 45px;
          padding: 0 0 7px;
          img {
            display: inline-block; } }
        tr {
          td:nth-last-child(1) {
            font-weight: 600; } } } }
    &-price {
      font-size: 18px;
      color: #ef3f4a;
      font-weight: 600;
      font-family: Tahoma;
      vertical-align: bottom;
      text-align: right;
      letter-spacing: -0.6px; }
    &-price-one {
      padding-left: 10px; }
    &-but {
      max-width: 165px;
      padding-top: 4px;
      a {
        color: #ffffff; } } }
  &__bt-cart {
    display: block;
    width: 100%;
    border: none;
    cursor: pointer;
    max-width: 165px;
    height: 32px;
    line-height: 32px;
    margin-left: auto;
    margin-right: auto;
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
    color: #ffffff;
    background: #ef3f4a;
    text-align: center;
    span {
      color: #ffffff;
      display: inline-block;
      padding-left: 27px;
      height: 15px;
      line-height: 15px;
      position: relative;
      text-transform: uppercase;
      &:before {
        content: '';
        background: url(../images/cart-icon.png) no-repeat left center;
        display: inline-block;
        width: 15px;
        height: 15px;
        position: absolute;
        left: 0;
        top: 0; } } }
  &__button {
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 36px;
    margin-bottom: 153px;
    @media (max-width: 800px) {
      margin-bottom: 50px; } }
  &.inn {
    padding-top: 20px;
    padding-bottom: 0px;
    @media (max-width: 650px) {
      .row {
        -ms-flex-pack:center !important {}
        justify-content:center !important {} } }
    .catalog-list__item {
      @media (max-width: 1024px) {
        margin-left: 10px;
        margin-right: 10px; }
      @media (max-width: 650px) {
        margin-left: 5px;
        margin-right: 5px; } } } }


.breadcrum {
  padding-bottom: 45px;
  ul {
    display: block;
    margin: 0;
    padding: 0;
    font-family:  Tahoma;
    &:after {
      content: '';
      display: table;
      clear: both; }
    li {
      letter-spacing: -0.3px;
      margin: 0;
      padding: 0;
      float: left;
      list-style: none;
      font-size: 14.26px;
      color: #bebebe;
      a {
        font-size: 14.26px;
        color: #797979;
        font-weight: 600;
        text-decoration: none;
        &:after {
          content: '';
          display: inline-block;
          width: 24px;
          height: 9px;
          background: url(../images/breadcram-arrow.png) no-repeat center; } } } } }

.popover {
  padding: 5px;
  .popover-title {
    display: none; } }

span[data-toggle=popover] {
  cursor: pointer; }
